import styled from 'styled-components';

export const MenuWrapper = styled.div`
  width: 220vw;
  height: 155vh;
  position: absolute;
  border-radius: 100%;
  top: -560px;
  left: -440px;
  background-color: ${({ theme }) => theme.colors.mainGreen};
  z-index: 1000;
  visibility: hidden;
  box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.1);
`;
export const MenuWrapperBehind = styled.div`
  width: 240vw;
  height: 155.7vh;
  position: absolute;
  border-radius: 100%;
  top: -560px;
  left: -440px;
  background-color: #5bd780;
  z-index: 999;
  visibility: hidden;
  box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.1);
`;

export const CloseMenu = styled.button`
  width: 60px;
  height: 60px;
  background-color: ${({ theme }) => theme.colors.mainGreen};
  position: absolute;
  bottom: 5%;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  border-radius: 100%;
  outline: none;
  box-shadow: 0 2px 5px 2px rgba(0, 0, 0, 0.3);
  visibility: hidden;
  svg {
    line {
      stroke-width: 11px;
      stroke: white;
    }
    width: 40%;
  }
`;

export const MenuList = styled.ul`
  list-style: none;
  padding: 0;
  margin: 0;
  position: absolute;
  top: 60px;
  left: 80px;
  z-index: 1001;
`;

export const MenuLink = styled.li`
  color: ${({ theme }) => theme.colors.white};
  font-size: 27px;
  margin-bottom: 30px;
  visibility: hidden;
`;
