import React from 'react';
import PropTypes from 'prop-types';
import { Form } from './RegisterForm.styles';
import Input from 'components/atoms/Input/Input';
import Button from 'components/atoms/Button/Button';

const RegisterForm = () => {
  const signUpHandler = (e) => {
    e.preventDefault();
  };

  return (
    <Form>
      <Input placeholder="Nazwa użytkownika" atr="text" />
      <Input placeholder="Hasło" atrs="password" />
      <Input placeholder="Email" atrs="email" />
      <Button handler={signUpHandler}>ZAREJESTRUJ SIĘ</Button>
    </Form>
  );
};

RegisterForm.propTypes = {};

export default RegisterForm;
