import React, { useRef } from 'react';
import PropTypes from 'prop-types';
import gsap from 'gsap';
import MenuTrigger from 'components/atoms/MenuTrigger/MenuTrigger';
import {
  MenuWrapper,
  MenuWrapperBehind,
  CloseMenu,
  MenuLink,
  MenuList,
} from './Menu.styles';
import { ReactComponent as CloseIcon } from 'assets/icons/closeIcon.svg';

const Menu = ({ openMenu }) => {
  let menuWrapper = useRef(null);
  let menuWrapperBehind = useRef(null);
  let menuCloseButton = useRef(null);
  let menuItem1 = useRef(null);
  let menuItem2 = useRef(null);
  let menuItem3 = useRef(null);
  let menuItem4 = useRef(null);

  const menuOn = () => {
    const tl = gsap.timeline({ defaults: { ease: 'power3.out' } });

    tl.fromTo(
      [menuWrapperBehind, menuWrapper],
      {
        autoAlpha: 0,
        scale: 0.000001,
      },
      { scale: 1, autoAlpha: 1, stagger: 0.1, duration: 0.4 }
    )
      .fromTo(
        menuCloseButton,
        { y: '+=100', autoAlpha: 0 },
        { autoAlpha: 1, y: 0, ease: 'back.out', duration: 0.4 },
        '-=0.3'
      )
      .fromTo(
        [menuItem1, menuItem2, menuItem3, menuItem4],
        {
          autoAlpha: 0,
          y: '+=40',
        },
        { autoAlpha: 1, y: 0, stagger: 0.1, duration: 0.2 }
      );
  };

  const menuOff = () => {
    const tl = gsap.timeline({ defaults: { ease: 'power3.out' } });

    tl.fromTo(
      [menuItem1, menuItem2, menuItem3, menuItem4],
      { autoAlpha: 1, y: 0 },
      {
        autoAlpha: 0,
        y: '+=40',
        stagger: 0.05,
        duration: 0.2,
      }
    )
      .fromTo(
        menuCloseButton,
        { autoAlpha: 1, y: 0 },
        { y: '+=100', autoAlpha: 0, ease: 'back.in', duration: 0.4 },
        '-=0.3'
      )
      .fromTo(
        [menuWrapperBehind, menuWrapper],
        { scale: 1, autoAlpha: 1 },
        {
          duration: 0.4,
          autoAlpha: 0,
          scale: 0.000001,
          stagger: 0.1,
        }
      );
  };

  const loginHandler = () => {
    menuOff();
    openMenu();
  };

  return (
    <>
      <MenuTrigger handler={menuOn} />
      <MenuWrapper ref={(el) => (menuWrapper = el)}></MenuWrapper>
      <MenuWrapperBehind
        ref={(el) => (menuWrapperBehind = el)}
      ></MenuWrapperBehind>
      <MenuList>
        <MenuLink ref={(el) => (menuItem1 = el)}>Home</MenuLink>
        <MenuLink ref={(el) => (menuItem2 = el)}>Register</MenuLink>
        <MenuLink ref={(el) => (menuItem3 = el)}>User</MenuLink>
        <MenuLink ref={(el) => (menuItem4 = el)} onClick={loginHandler}>
          Login
        </MenuLink>
      </MenuList>
      <CloseMenu ref={(el) => (menuCloseButton = el)} onClick={menuOff}>
        <CloseIcon />
      </CloseMenu>
    </>
  );
};

Menu.propTypes = {
  openMenu: PropTypes.func,
};

export default Menu;
